//  
export function setOption1(countryAddConfirmRankList) {
  let xAxisData = []
  let seriesData = []
  for (let item of countryAddConfirmRankList) {
    xAxisData.push(item.nation)
    seriesData.push(item.addConfirm)
  }
  let option = {
    color: ["#2f89cf"],
    tooltip: {
      trigger: "axis",
      axisPointer: {
        // 
        type: "shadow" // 'line' | 'shadow'
      }
    },
    // 
    grid: {
      left: "0%",
      top: "10px",
      right: "3%",
      bottom: "4%",
      containLabel: true
    },
    xAxis: [{
      type: "category",
      data: xAxisData,
      axisTick: {
        alignWithLabel: true
      },
      //  
      axisLabel: {
        color: "rgba(255,255,255,.6) ",
        fontSize: "10"
      },
      // x
      axisLine: {
        show: false
      }
    }],
    yAxis: [{
      type: "value",
      //  
      axisLabel: {
        color: "rgba(255,255,255,.6) ",
        fontSize: 10
      },
      // y2
      axisLine: {
        lineStyle: {
          color: "rgba(255,255,255,.1)",
          width: 2
        }
      },
      // y
      splitLine: {
        lineStyle: {
          color: "rgba(255,255,255,.1)"
        }
      }
    }],
    series: [{
      name: "",
      type: "bar",
      data: seriesData,
      barWidth: "35%",
      itemStyle: {
        barBorderRadius: 5
      }
    }]
  }
  return option
}


// 
export function setOption2(foreignList) {
  let yAxis1 = []
  let yAxis2 = []
  let rate = []
  let countryList = [0, 1, 9, 41, 4]
  for (let i of countryList) {
    yAxis1.push(foreignList[i].name)
    let cureRate = ((foreignList[i].heal / foreignList[i].confirm) * 100).toFixed(0)
    yAxis2.push(foreignList[i].heal)
    rate.push(cureRate)

  }
  // 
  let myColor = ["#1089E7", "#F57474", "#56D0E3", "#F8B448", "#8B78F6"];
  let option = {
    grid: {
      top: "10%",
      left: "22%",
      bottom: "10%",
      // containLabel: true
    },
    xAxis: {
      show: false
    },
    yAxis: [{
        type: 'category',
        inverse: true,
        data: yAxis1,
        axisLine: {
          show: false
        },
        axisTick: {
          show: false
        },
        axisLabel: {
          color: "#fff"
        },
      },
      {
        data: yAxis2,
        inverse: true,
        // y
        axisLine: {
          show: false
        },
        // 
        axisTick: {
          show: false
        },
        // 
        show: false,
        axisLabel: {
          color: "#fff"
        }
      }
    ],
    series: [{
        name: '',
        type: 'bar',
        data: rate,
        yAxisIndex: 0,
        // 
        barCategoryGap: 50,
        //
        barWidth: 10,

        itemStyle: {
          normal: {
            // 
            barBorderRadius: 20,
            color: function (params) {
              // params 
              // console.log(params);
              // dataIndex 
              return myColor[params.dataIndex];
            }
          },

        },
        // 
        label: {
          normal: {
            show: true,
            // 
            position: "inside",
            // 
            formatter: "{c}%"
          }
        },
      },
      {
        name: '',
        type: 'bar',

        data: [100, 100, 100, 100, 100],
        yAxisIndex: 1,
        // 
        barCategoryGap: 50,
        //
        barWidth: 15,
        itemStyle: {
          normal: {
            color: "none",
            borderColor: "#00c1de",
            borderWidth: 3,
            barBorderRadius: 15
          },

        },
      }
    ]
  };
  return option
}


