export function setOption6(importStatis) {
  let data = []
  for (let item of importStatis) {
    // console.log(item);
    data.push({
      name: item.province,
      value: item.importedCase
    })
  }
  let option = {
    color: ['#006cff', '#60cda0', '#ed8884', '#ff9f7f', '#0096ff', '#9fe6b8', '#32c5e9', '#1d9dff'],
    tooltip: {
      trigger: 'item',
      formatter: '{a} <br/>{b} : {c} ({d}%)'
    },
    series: [{
      name: '',
      type: 'pie',
      radius: ['10%', '70%'],
      center: ['50%', '50%'],
      roseType: 'radius',
      data: data
    }],
    //  
    label: {
      fontSize: 10
    },
    labelLine: {
      // 
      length: 6,
      // 
      length2: 8
    }

  };
  return option
}

