// 
export function setForeignData(foreignList) {
  let confirmData = []
  let nowConfirmData = []
  for (let item of foreignList) {
    confirmData.push({
      name: item.name,
      value: item.confirm
    })
    nowConfirmData.push({
      name: item.name,
      value: item.nowConfirm
    })
  }
  let foreignData = {
    confirmData,
    nowConfirmData
  }
  return foreignData
}