<template>
  <div class="time">
    {{ showDate }}
  </div>
</template>

<script>
export default {
  name: "ZpTime",
  data() {
    return {
      date: new Date(),
    };
  },
  computed: {
    showDate() {
      let y = this.date.getFullYear() + "";
      let M = this.date.getMonth() + 1 + "";
      let d = this.date.getDate() + "--";
      let h = this.date.getHours() + "";
      let m = this.date.getMinutes() + "";
      let s = this.date.getSeconds() + "";
      return y + M + d + h + m + s;
    },
  },
  mounted() {
    setInterval(() => {
      this.date = new Date();
    }, 1000);
  },
};
</script>

